<template>
    <transition-group :name="transition" class="breadcrumb" tag="ol">
        <li v-for="(item, index) in list" :key="item.path" :class="{ 'breadcrumb-item': true, active: isLast(index) }">
            <template v-if="isLast(index)">{{ showName(item) }}</template>
            <template v-else-if="item.redirect">
                <router-link v-if="item.redirect !== 'noredirect'" :to="item.redirect">{{ showName(item) }}</router-link>
                <template v-else>{{ showName(item) }}</template>
            </template>
            <router-link v-else :to="item">{{ showName(item) }}</router-link>
        </li>
    </transition-group>
</template>

<script>
    export default {
        name: 'Breadcrumbs',
        props: {
            list: {
                type: Array,
                required: true,
            },
            transition: {
                type: String,
                default: 'breadcrumb',
            },
        },
        methods: {
            isLast(index) {
                return index === this.list.length - 1;
            },
            showName(item) {
                if (item.meta && item.meta.label) {
                    return item.meta.label;
                }
                if (item.name) {
                    return item.name;
                }

                return '';
            },
        },
    };
</script>
