                    //         },
                    //         {
                    //             title: 'Icons',
                    //             to: '/ui/icons',
                    //         },
                    //         {
                    //             title: 'Buttons',
                    //             to: '/ui/buttons',
                    //         },
                    //     ],
                    // },
                ],
            };
